.header {
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-size: 20px;
  padding: 0 16px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);

  @media (max-width: 600px) {
    padding: 0 10px;
  }

  &__logo {
    height: 70px;
  }

  &__nav {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  &__title {
    margin: 0;
    color: black;

    @media (max-width: 600px) {
      font-size: 20px;
    }
  }

  &__link {
    color: black;
    margin-right: 15px;

    &:last-child {
      margin-right: 0;
    }

    @media (max-width: 600px) {
      font-size: 16px;
    }

    @media (max-width: 340px) {
      margin-right: 10px;
    }

    &-container {
      display: flex;
      justify-content: center;
      align-items: center;
      list-style-type: none;
      padding: 0;

      @media (max-width: 600px) {
        padding-left: 10px;
      }
    }
  }

  &__link > a {
    color: black;
  }
}
